.appWrapper {
    display: flex;
    justify-content: center;

    .wrapper {
        margin: 50px 0;
        margin-right: calc(-1 * (100vw - 100%));
        background-color: #ffffff;
        box-sizing: 0px 10px 20px rgba(0, 0, 0, 0.04);
        border-radius: 20px;
        width: 1080px;

        .content {
            display: flex;
            flex-direction: column;
            padding: 40px;

            .wrapperSneakers {
                display: flex;
                align-items: center;
                justify-content: space-between;
                margin-bottom: 40px;

                .searchBlock {
                    border: 1px solid #f3f3f3;
                    display: flex;
                    border-radius: 13px;
                    padding: 0 15px;
                    position: relative;

                    .clearbtn {
                        position: absolute;
                        right: 10px;
                        align-self: center;
                        width: 18px;
                        height: 18px;
                        cursor: pointer;
                    }

                    input {
                        border: 0;
                        padding: 10px;
                        font-size: 16px;
                        width: 200px;
                        outline: none;
                    }
                }
            }

            h1 {
                margin: 0;
            }
        }
    }

    .sneakers {
        margin-top: 20px;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }
}
